import React, { useState } from "react";
import { Form, Button } from "semantic-ui-react";
import "semantic-ui-css/semantic.min.css";
import { useForm } from "react-hook-form";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function RegistrationForm() {
  const {
    register,
    formState: { errors },
  } = useForm();

  const [firstName, setFirstName] = useState("");
  const [lastName, setlastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [cpassword, setCpassword] = useState("");
  const [userName, setUserName] = useState("");
  const [profile, setProfile] = useState("");

  const navigate = useNavigate();

  const onSubmit = async () => {
    try {
      if (firstName && lastName && email && userName && password) {
        const data = new FormData();

        data.append("file", profile);
        data.append(
          "data",
          JSON.stringify({
            firstName,
            lastName,
            email,
            password,
            userName,
            cpassword,
          })
        );

        const res = await axios.post(
          "http://localhost:5000/api/register",
          data,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        );
        if (res.status === 200) {
          alert("registration successfully");
          navigate("/users");
        }
      }
    } catch (error) {
      alert(error.response.data.message);
    }
  };

  return (
    <div
      style={{
        marginTop: "100px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <h1>Registration FORM</h1>
      <Form encType="multipart/form-data" onSubmit={onSubmit}>
        <Form.Field>
          <label>First Name</label>
          <input
            placeholder="First Name"
            type="text"
            // {...register("firstName", { required: true, maxLength: 10 })}
            value={firstName}
            onChange={(e) => {
              setFirstName((p) => e.target.value);
            }}
          />
        </Form.Field>
        {errors.firstName && (
          <p style={{ color: "red" }}>Please check the First Name</p>
        )}
        <Form.Field>
          <label>Last Name</label>
          <input
            placeholder="Last Name"
            type="text"
            // {...register("lastName", { required: true, maxLength: 10 })}
            value={lastName}
            onChange={(e) => {
              setlastName(() => e.target.value);
            }}
          />
        </Form.Field>
        {errors.lastName && (
          <p style={{ color: "red" }}>Please check the Last Name</p>
        )}
        <Form.Field>
          <label>User Name</label>
          <input
            placeholder="Last Name"
            type="text"
            // {...register("lastName", { required: true, maxLength: 10 })}
            value={userName}
            onChange={(e) => {
              setUserName(() => e.target.value);
            }}
          />
        </Form.Field>
        {errors.lastName && (
          <p style={{ color: "red" }}>Please check the User Name</p>
        )}
        <Form.Field>
          <label>Email</label>
          <input
            // {...register("email", {
            //   required: true,
            //   pattern:
            //     /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            // })}
            placeholder="Email"
            type="email"
            value={email}
            onChange={(e) => {
              setEmail(() => e.target.value);
            }}
          />
        </Form.Field>
        {errors.email && <p style={{ color: "red" }}>Please check the Email</p>}
        <Form.Field>
          <label>Password</label>
          <input
            // {...register("password", {
            //   required: true,
            //   pattern: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,15}$/,
            // })}
            placeholder="Password"
            type="password"
            value={password}
            onChange={(e) => {
              setPassword(() => e.target.value);
            }}
          />
        </Form.Field>
        {errors.password && (
          <p style={{ color: "red" }}>Please check the Password</p>
        )}
        <Form.Field>
          <label>Confirm Password</label>
          <input
            placeholder="Password"
            type="password"
            // {...register("password", {
            //   required: true,
            //   pattern: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,15}$/,
            // })}
            value={cpassword}
            onChange={(e) => {
              setCpassword(() => e.target.value);
            }}
          />
        </Form.Field>
        {errors.password && (
          <p style={{ color: "red" }}>Please check confirm Password</p>
        )}
        <Form.Field>
          <label>Profile Picture</label>
          <input
            placeholder="Password"
            onChange={(e) => {
              setProfile(() => e.target.files[0]);
            }}
            type="file"
          />
        </Form.Field>
        <Button type="submit">Submit</Button>
      </Form>
    </div>
  );
}
