import React, { useEffect, useState } from "react";
import { Form, Button } from "semantic-ui-react";
import "semantic-ui-css/semantic.min.css";
import { useForm } from "react-hook-form";
import axios from "axios";
import { useNavigate, useSearchParams } from "react-router-dom";

export default function EditForm() {
  const {
    register,
    formState: { errors },
  } = useForm();
  const navigate = useNavigate();

  const [searchParams, setSearchParams] = useSearchParams();

  const [firstName, setFirstName] = useState("");
  const [lastName, setlastName] = useState("");
  const [email, setEmail] = useState("");
  const [userName, setUserName] = useState("");
  const [profile, setProfile] = useState("");

  const fetchSingleUser = async (id) => {
    try {
      const { data } = await axios.get("http://localhost:5000/api/users/" + id);

      if (data.data) {
        setFirstName(data.data.firstName);
        setlastName(data.data.lastName);
        setEmail(data.data.email);
        setUserName(data.data.userName);
        setProfile(data.data.profile);
      }
    } catch (error) {}
  };

  useEffect(() => {
    const id = searchParams.get("id");
    fetchSingleUser(id);
  }, []);

  const onSubmit = async () => {
    try {
      if (firstName || lastName) {
        const data = new FormData();

        data.append("file", profile);
        data.append(
          "data",
          JSON.stringify({
            firstName,
            lastName,
          })
        );

        const res = await axios.put(
          "http://localhost:5000/api/users/" + searchParams.get("id"),
          data,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        );

        if (res.status === 200) {
          alert("updated successfully");
          navigate("/users");
        }
      }
    } catch (error) {
      alert(error.response.data.message);
    }
  };

  return (
    <div
      style={{
        marginTop: "100px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <h1>Edit User</h1>
      <Form encType="multipart/form-data" onSubmit={onSubmit}>
        <Form.Field>
          <label>First Name</label>
          <input
            placeholder="First Name"
            type="text"
            // {...register("firstName", { required: true, maxLength: 10 })}
            value={firstName}
            onChange={(e) => {
              setFirstName((p) => e.target.value);
            }}
          />
        </Form.Field>
        {errors.firstName && (
          <p style={{ color: "red" }}>Please check the First Name</p>
        )}
        <Form.Field>
          <label>Last Name</label>
          <input
            placeholder="Last Name"
            type="text"
            // {...register("lastName", { required: true, maxLength: 10 })}
            value={lastName}
            onChange={(e) => {
              setlastName(() => e.target.value);
            }}
          />
        </Form.Field>
        {errors.lastName && (
          <p style={{ color: "red" }}>Please check the Last Name</p>
        )}
        <Form.Field>
          <label>User Name</label>
          <input
            placeholder="Last Name"
            type="text"
            disabled={true}
            // {...register("lastName", { required: true, maxLength: 10 })}
            value={userName}
          />
        </Form.Field>
        {errors.lastName && (
          <p style={{ color: "red" }}>Please check the User Name</p>
        )}
        <Form.Field>
          <label>Email</label>
          <input
            // {...register("email", {
            //   required: true,
            //   pattern:
            //     /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            // })}
            placeholder="Email"
            type="email"
            value={email}
            disabled={true}
          />
        </Form.Field>
        {errors.email && <p style={{ color: "red" }}>Please check the Email</p>}
        <Form.Field>
          <label>Profile Picture</label>
          <input
            placeholder="Password"
            onChange={(e) => {
              setProfile(() => e.target.files[0]);
            }}
            type="file"
          />
        </Form.Field>
        <Button type="submit">Submit</Button>
      </Form>
    </div>
  );
}
